  .h2, h2 {
    font-size: 29px;
  }

  .h1, h1 {
    font-size: 27px;
  }
}
html {
  font-size: 100%;
}

body {
  font-family: "Montserrat", "Helvetica Neue", "Helvetica", "Hiragino Sans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  font-weight: 500;
  font-size: 1rem;
  color: #181717;
  background-color: #fff;
  overflow-x: hidden;
}

.container {
  max-width: 1255px;
  padding: 0 13px 0px 23px;
  margin: 0 auto;
}

.three-row, .three-row-v2 {
  display: flex;
  flex-flow: wrap;
}
.three-row .row, .three-row .item, .three-row-v2 .row, .three-row-v2 .item {
  width: 33.3%;
}

.three-row {
  justify-content: space-between;
}

.three-row-v2 {
  justify-content: flex-start;
}
.three-row-v2 .item {
  margin-right: 20px;
}

.card {
  transition: all ease 0.3s;
  border-radius: 15px;
  padding: 0px 15px 15px;
}
.card .card-img {
  height: 357px;
  width: 336px;
  display: flex;
  align-items: center;
}
.card .card-img img {
  object-fit: cover;
  width: 100%;
}
.card .card-content {
  padding-left: 16px;
  padding-top: 5px;
}
.card .card-content a.header-card {
  transition: all ease 0.3s;
  display: block;
}
.card .card-content a.header-card:hover {
  text-decoration: underline;
}
.card .small_text {
  margin-bottom: 15px;
}

.item {
  padding: 15px;
  border-radius: 10px;
  cursor: pointer;
}
.item:hover {
  box-shadow: -6px 7px 20px #e2e2e2;
}

.sizes_block {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
  margin-bottom: 9px;
}
.sizes_block .size {
  align-items: center;
  align-self: center;
  background-color: #f7f7f7;
  border-radius: 100%;
  color: #181717;
  display: flex;
  font-family: inherit;
  font-size: 13px;
  font-style: normal;
  font-weight: 600;
  height: 52px;
  justify-content: center;
  line-height: 160%;
  line-height: 52px;
  margin-bottom: 10px;
  margin-right: 14px;
  text-align: center;
  text-decoration: none;
  transition: all 0.3s ease;
  width: 57px;
  line-height: 9px;
  height: 58px;
  padding: 14px 11px;
  word-break: break-word;
}
.sizes_block .size:hover, .sizes_block .size.active {
  color: #fff;
  background-color: #57C645;
  text-decoration: none;
}

.characters-table {
  padding: 0px 40px 10px 0px;
}
.characters-table .line {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 43px;
  padding: 6px 5px 7px 0;
  text-align: left;
}
.characters-table .line:not(:last-child) {
  border-bottom: 1px solid #D1D1D1;
}
.characters-table .small_text {
  margin-bottom: 0px;
  overflow: hidden;
  text-overflow: ellipsis;
  text-transform: none;
  white-space: nowrap;
  width: 80%;
}

.new-slider {
  position: relative;
  margin-bottom: 80px;
}
.new-slider .slide {
  background-color: #e2e2e2;
  margin: 20px;
  width: 1255px;
  height: 686px;
  overflow: hidden;
  position: relative;
}
.new-slider .wrap-main-img {
  width: 100%;
  height: 100%;
}
.new-slider .wrap-main-img img {
  width: 100%;
  height: 100%;
  display: block;
  margin-right: 0px;
  object-fit: cover;
}

.slick-slider {
  text-align: center;
  /*     */
}

.slick-slide {
  display: inline-block;
  /*   ,     */
  vertical-align: top;
  /*   */
}
.slick-slide img {
  width: 100%;
}

.slick-center .slick-slide {
  transition: all ease 0.3s;
  width: 1255px !important;
  transition: transform 0.3s ease;
  /*    */
  margin: 17px 31px 20px 59px;
}

.nav-block {
  position: absolute;
  right: 0px;
  top: 748px;
  display: flex;
}

.custom-nav {
  z-index: 2 !important;
  width: 40px !important;
  height: 40px !important;
  background-color: #F5F5F5;
  z-index: 99;
  cursor: pointer;
  display: flex;
}
.custom-nav img {
  margin: 0 auto;
  width: 7px;
}
.custom-nav.custom-prev-button {
  right: 64px;
}
.custom-nav:hover {
  opacity: 0.6;
}
.custom-nav .custom-next-button {
  right: -17px;
  background-position: center;
  background-color: #F5F5F5;
}

.gallery-block {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 80px;
}

.left-gallery {
  position: relative;
  width: 65%;
}
.left-gallery .nav-block .custom-prev-button,
.left-gallery .nav-block .custom-next-button {
  background-color: #fff;
  opacity: 0.6;
}
.left-gallery .custom-nav:hover {
  opacity: 1;
}

.left-gallery .nav-block {
  top: auto;
  bottom: 0px;
  right: 0px;
}

.right-gallery {
  width: 385px;
}
.right-gallery .video-gallery {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.video-description {
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 151.278%;
  /* 24.205px */
  letter-spacing: 0.16px;
  transition: all ease 0.3s;
}
.video-description:hover {
  text-decoration: underline;
}

.video-wrap {
  margin-bottom: 15px;
}
.video-wrap iframe {
  width: 100%;
  height: 167px;
}

.slider-left .wrap-main-img {
  height: 481px;
}
.slider-left .wrap-main-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slick-slide {
  transition: all 0.3s;
}

.slick-dots {
  display: flex;
  justify-content: flex-start;
  margin: 0 auto;
  padding-left: 0;
  position: relative;
  width: 1260px;
  justify-content: flex-start;
}
.slick-dots li {
  width: 172px;
  height: 3px;
  color: #000;
  font-size: 0px;
  margin: 0px;
  background-color: #ccc;
  cursor: pointer;
}
.slick-dots li.slick-active {
  background-color: #181717;
}
.slick-dots li:hover {
  background-color: #181717;
}

.full-screen-block {
  margin-bottom: 100px;
  position: relative;
}
.full-screen-block h2, .full-screen-block .h2 {
  margin-bottom: 35px;
  padding-left: 4px;
}
.full-screen-block .container {
  position: relative;
}
.full-screen-block:before {
  content: "";
  width: 785px;
  height: 878px;
  background-color: #F5F5F5;
  position: absolute;
  right: 0px;
  top: 0px;
}

.news-block {
  display: flex;
  position: absolute;
  bottom: 57px;
  left: 105px;
  z-index: 9;
  justify-content: flex-start;
  align-items: flex-end;
  color: #F5F5F5;
}
.news-block p {
  color: #fff;
}
.news-block .new-name {
  margin-bottom: 25px;
  color: #F5F5F5;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 105%;
  /* 18.9px */
  text-align: left;
}
.news-block .new-header {
  margin-bottom: 26px;
  color: #F5F5F5;
  font-size: 67.087px;
  font-style: normal;
  font-weight: 700;
  line-height: 105%;
  /* 70.442px */
  text-align: left;
  text-align: left;
}
.news-block .new-subscribtion {
  color: #F5F5F5;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 149%;
  /* 26.82px */
}
.news-block .part-left {
  text-align: left;
  max-width: 60%;
}
.news-block .part-right {
  padding-bottom: 39px;
  padding-left: 38px;
}
.news-block .news-link {
  display: flex;
  align-items: center;
  transition: all ease 0.3s;
  color: #F5F5F5;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 140%;
  /* 28px */
}
.news-block .news-link span {
  margin-right: 30px;
}
.news-block .news-link:hover {
  transform: scale(1.1);
}

.button {
  display: flex;
  width: 100%;
  height: 56px;
  padding: 20px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  color: #FFF;
  background: #57C645;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  transition: all ease 0.3s;
}
.button:hover {
  background-color: #3eb72b;
}

.grey-bg {
  background-color: #F5F5F5;
  padding: 80px 0px;
}

.three-in-row-wrap {
  display: flex;
  flex-flow: wrap;
}
.three-in-row-wrap .item {
  margin-bottom: 47px;
  margin-right: 29px;
  width: 386px;
}
.three-in-row-wrap .item:nth-child(3n) {
  margin-right: 0px;
}
.three-in-row-wrap .wrap-item-image {
  width: 100%;
  height: 289px;
  margin-bottom: 20px;
  overflow: hidden;
  display: block;
}
.three-in-row-wrap .wrap-item-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all ease 0.3s;
}
.three-in-row-wrap .wrap-item-image:hover img {
  transform: scale(1.1);
}
.three-in-row-wrap .item-subheader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.three-in-row-wrap .item-subheader {
  margin-bottom: 3px;
}
.three-in-row-wrap .full-text {
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 160%;
  /* 32px */
  transition: all ease 0.3s;
}
.three-in-row-wrap .full-text:hover {
  text-decoration: underline;
}

@media screen and (max-width: 1300px) {
