}

.overlay-header {
  background-color: rgba(0, 0, 0, 0.8);
  height: 400px;
  width: 100%;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
}

header.open .overlay-header {
  opacity: 1;
}

.center-nemu {
  align-self: center;
  display: flex;
  align-self: center;
  display: flex;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.center-nemu li {
  margin-right: 80px;
}

.for-mobile {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-top: 10px;
}
.for-mobile .menu.center-nemu {
  align-self: center;
  display: flex;
}

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
  transition: all ease 0.3s;
  padding-top: 29px;
}
.menu li:not(:last-child) {
  margin-right: 77px;
}
.menu li .dropbtn {
  margin-right: 14px;
}
.menu li:hover {
  color: #57C645;
}
.menu li .active {
  color: #57C645;
}
.menu .center-nemu li:not(:last-child) {
  margin-right: 45px;
}

.menu-button {
  display: none;
}

.close-button {
  display: none;
}

.menu-link {
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
}

.menu a {
  color: #F5F5F5;
  font-family: Montserrat;
  font-size: 15px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  letter-spacing: 1.8px;
  text-transform: uppercase;
}

/* Dropup Button */
.sub-menu {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.sub-menu .row {
  width: 245px;
}

.current-menu-item {
  color: #57C645;
}

.menu li {
  transition: all ease 0.3s;
  z-index: 999;
}

.menu li.current-menu-item a {
  color: #57C645;
}

.menu li:hover a {
  color: #57C645;
}

.menu li:hover .sub-menu a {
  color: #fff;
}

.menu li.current-menu-item .sub-menu li.current-menu-item a,
.menu li:hover .sub-menu a:hover {
  color: #57C645;
}

.menu-item-has-children.dropup .sub-menu a::after {
  display: none;
}

.dropup:hover .sub-menu {
  display: flex;
  opacity: 1;
  pointer-events: all;
}

/* The container <div> - needed to position the dropup content */
.dropup {
  position: relative;
  display: inline-block;
}

/* Dropup content (Hidden by Default) */
.sub-menu {
  opacity: 0;
  transition: all ease 0.3s;
  pointer-events: none;
  display: flex;
  position: absolute;
  top: 10px;
  padding-top: 20px;
  background-color: transparent;
  z-index: 1;
  color: #fff;
  list-style: none;
  padding-left: 0px;
  margin-left: 0px;
  flex-direction: row;
  max-height: 352px;
  min-width: 700px;
  flex-flow: column wrap;
}
.sub-menu a {
  color: #fff;
}

.menu ul.sub-menu li {
  margin-right: 0px;
}

