  margin-bottom: 20px;
}
.wrap-footer-inner .links-part {
  text-align: left;
}
.wrap-footer-inner .links-part .footer-heading {
  color: #F5F5F5;
  font-size: 22px;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
  /* 33px */
  margin-bottom: 30px;
}
.wrap-footer-inner .links-sub {
  font-weight: 600;
}
.wrap-footer-inner .links-link {
  color: #D1D1D1;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  transition: all ease 0.3s;
  display: block;
  line-height: 150%;
}
.wrap-footer-inner .links-link:hover {
  color: #57C645;
}

.icon-social {
  display: flex;
  margin-bottom: 30px;
}
.icon-link {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  border: 1px solid #ccc;
  margin-right: 10px;
  transition: all ease 0.3s;
}
.icon-link:hover {
  border: 1px solid #57C645;
}
.icon-link:hover svg path {
  fill: #57C645;
}
.icon-image {
  width: 14px;
  display: block;
}

.mart a {
  transition: all ease 0.3s;
}
.mart a:hover {
  color: #57C645;
}

@media screen and (max-width: 1010px) {
  .links-part {
    width: 50%;
    margin-bottom: 40px;
  }

  .wrap-footer-inner .to-be-bottom {
    width: 50%;
    order: 4;
    align-self: flex-start;
  }

  .products-links {
    padding-top: 110px;
  }
}
@media screen and (max-width: 600px) {
  .links-part {
    width: 100%;
    text-align: center;
  }

  .wrap-footer-inner .to-be-bottom {
    width: 100%;
  }

  .wrap-footer-inner .links-part {
    text-align: center;
  }

  .wrap-footer-inner .logo {
    left: 50%;
    transform: translateX(-50%);
    right: auto;
    margin: 0 auto;
  }

  .to-be-bottom {
    text-align: center;
  }

  .icon-social {
    justify-content: center;
  }

  .l-footer {
    padding: 50px 0px;
  }
}