@use '../helpers' as h;


.all-news {
    padding: 100px 0px;
    .heading-thin{
        text-transform: none;
    }

}

.one-new{
    margin-bottom: 90px;
}

.top-line{
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    margin-bottom: 40px;
    .date{
        width: 20%;
        font-weight: 300;
        padding-top: 9px;
        display: block;
    }
    .heading-thin {
        padding-left: 15px;
        width: 80%;
    }
}

.news-illustration {
    img{
        width: 100%;
        height: 444px;
        object-fit: cover;
        margin-bottom: 40px;;
    }
    .normal-text{
        padding-left: 260px;
        font-size: 18px;
        font-style: normal;
        font-weight: 500;
        max-width: 800px;
        line-height: 150%;
    }
}


.article-content{
    padding: 100px 0px;
    .article-image{
        height: 700px;
        width: 100%;
        margin-bottom: 60px;
        img, iframe{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}


.article-content{
    blockquote{
        font-size: 27px ;
        font-style: italic;
        color : #ccc; 
        width: 82.5%;
        margin: 0 auto 30px;    
    }
    margin: 0 auto 50px;
    div{
        width: 82.5%;
        margin: 0 auto 10px;
    }
    h2{
        margin: 40px auto 20px;
    }
    h3, h4{
        width: 82.5%;
        margin: 30px auto 20px;
    }
    p, h2 {
        width: 82.5%;
    }
    p {
        font-size: 18px;
        font-weight: 500;
        letter-spacing: inherit;
        line-height: 161%;;
        margin-bottom: 25px;;
        margin: 0 auto 20px;;
        color: #525252;
    }
    img{
        margin: 20px 0px;
        width: 100%;
        height: 100%;
        object-fit: cover;

    }
    iframe{
        width: 100%;
        height: 100%;
        min-height: 500px;
        object-fit: cover;
        margin: 50px 0px;
    }
    .two-images{
        // width: 100%;
        margin-bottom: 55px;
    }
    ul {
        width: 82.5%;
        margin: 0 auto 10px;
        padding-left: 100px;
    }       
    ul {
        list-style: none;
        padding: 30px 15px;
        li{
            padding-left: 15px;
            margin-bottom: 30px;
            position: relative;
            font-size: 18px;
            font-weight: 500;
            letter-spacing: inherit;
            line-height: 161%;;
            margin-bottom: 25px;;
            margin: 0 auto 20px;;
            color: #525252;
        }
        li:before{
            content: "";  
            width: 10px;
            height: 10px;
            position: absolute;
            display: block;
            left: 3px;
            top: 9px;
            border-radius: 100%;;
            background-color: h.$accent-color; 
            font-weight: bold; 
            display: inline-block; 
            margin-left: -15px; 
        }
    }
}


.two-images{
    display: flex;
    justify-content: space-between;
    margin-bottom: 90px;
    .image-part, div{
        width: 49%;
        height: 335px;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

    }
}



.list {
    list-style: none;
    // padding-left: 15px;
    padding: 30px 15px;
    .list-li{
        padding-left: 15px;
        margin-bottom: 30px;
        position: relative;
    }
    .list-li:before{
        content: "";  
        width: 10px;
        height: 10px;
        position: absolute;
        display: block;
        left: 3px;
        top: 9px;
        border-radius: 100%;;
        background-color: h.$accent-color; 
        font-weight: bold; 
        display: inline-block; 
        margin-left: -15px; 
    }
}

.author-line {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 50px;
    .author{
        color:  #525252;
        font-size: 18px;
        font-style: normal;
        font-weight: 300;
        line-height: 160%; /* 28.8px */
    }
}

.last-new{
    padding: 80px 0px 180px;

}


@media screen and (max-width:980px){
    .news-illustration .normal-text{
        padding-left: 23%;
    }
}


@media screen and (max-width: 760px){
    .news-illustration .normal-text{
        padding-left: 0%;
    }
    .top-line{
        flex-direction: column;
    }
    .top-line .date{
        width: 100%;
    }
    .top-line .heading-thin{
        width: 100%;
        padding-left: 0px;
    }
    .all-news {
        padding: 52px 0;
        .heading, .heading-thin {
            font-size: 22px;
            font-weight: 600;
        }
        .top-line .date {
            margin-bottom: 13px;
        }
    }

    .top-line{
        margin-bottom: 25px;
    }
    .news-illustration img {
        height: 220px;
        margin-bottom: 25px;
    }
    .one-new {
        margin-bottom: 77px;
    }
    .article-content .article-image{
        height: 192px;
    }

    .article-content {
        padding: 60px 0;
        h2, .h2{
            font-size: 25px;
            margin-bottom: 27px;
        }
    }
    .last-new{
        h2, .h2{
            font-size: 25px;
            margin-bottom: 27px;
        }
    }
    .two-images .image-part {
        height: 100%;
        width: 100%;
    }
    .two-images{
        margin-bottom: 39px;
        flex-direction: column;
    }
    .author-line{
        flex-direction: column;
    }
    .last-new {
        padding: 0px 0 80px;
    }
    .article-content{
        margin: 0 auto 30px;
        .two-images{
            margin-bottom: 10px;
        }
        blockquote{
            font-size: 20px;
            width: 95%;
        }
        div{
            width: 100%;
            margin: 0 auto 10px;
        }
        h2{
            margin: 30px auto 15px;
        }
        h3, h4 {
            margin: 30px auto 15px;
            width: 100%;
        }
        p, h2 {
            width: 100%;
        }

        img{
            margin: 20px 0px;
            min-height:300px;    
        }
        iframe{

            min-height: 400px;
            object-fit: cover;
            margin: 30px 0px;
        }

    }
}

