.highlight {
    position: relative;
    margin-bottom: .5em;
    margin-left: -1.5em;
    width: 100%;
    padding: 0 1.5em;
    background-color: #F5FAFC
}

pre code:before {
    display: block;
    position: absolute;
    right: 3px;
    top: 6px;
    padding: 3px 7px 0;
    color: #889499;
    font-size: 12px;
    line-height: 13px
}

code.html:before {
    content: 'HTML'
}

code.javascript:before {
    content: 'JS'
}

code.css:before {
    content: 'CSS'
}

